    <div class="SideSelection">
      <div :class="{ bounce: front }">
        <button
          @click="frontToggle()"
          :class="{
            ' button button__active': front,
            ' button button__disable': !front,
          }"
          :disabled="front"
        >
          <p>Front</p>
        </button>
      </div>
      <div :class="{ bounce: !front }">
        <button
          @click="frontToggle()"
          :class="{
            'button button__active': !front,
            'button button__disable': front,
          }"
          :disabled="!front"
        >
          Back
        </button>
      </div>
    </div>
  </main>
  <footer id="focus" v-show="currentMuscle != ''" class="exercises">
    <div  v-for="t in filtredExercises" :key="t">
      <div
        @click="
          if (t.Active != true) {
            t.Active = true;
          } else {
            t.Active = false;
          }
        "
        class="exercise__closed"
      >
        {{ t.name }}
      </div>

      <transition name="fade">
        <div class="exercise__opended" v-show="t.Active != false">
          <!--Карточка слева -->
          <div class="exercise__text">
            <br />
            <div class="instruction">
              <p class="instruction__text">Instruction:</p>
              <ol>
                <li
                  class="instruction__list"
                  v-for="any in t.description.split('. ')"
                  :key="any"
                >
                  {{ any }} {{ any.endsWith(". ") ? " " : "." }}
                </li>
              </ol>
            </div>
            <br />
            <div class="tips">
              <p class="tips__text">Tips:</p>
              <ul>
                <li
                  class="tips__list"
                  v-for="any in t.tips.split('. ')"
                  :key="any"
                >
                  {{ any }} {{ any.endsWith(".") ? " " : "." }}
                </li>
              </ul>
            </div>
            <br />
          </div>
          <!--Видео упражнения -->
          <sidebar class="exercise__video">
            <iframe class="exercise__youtube" :src="t.video"> </iframe>
          </sidebar>
        </div>
      </transition>
    </div>
  </footer>
</template>

<script>
import ExercisesData from "./data/ExercisesData.json";
export default {
  name: "App",
  data() {
    return {
      currentMuscle: "",
      front: true,
      disabled: false,
      exercises: ExercisesData,
      filtredExercises: [],
    };
  },
  methods: {
    ScrollClick() {
      setTimeout(() => {
        document
          .getElementById("focus")
          .scrollIntoView({ block: "start", behavior: "smooth" });
      }, 50);
    },
    filtredMuscle() {
      this.filtredExercises = "";
      this.filtredExercises = this.exercises.filter((exercise) =>
        exercise.muscle.includes(this.currentMuscle)
      );
    },
    frontToggle() {
      this.front = !this.front;
    },
    warnDisabled() {
      this.disabled = true;
      setTimeout(() => {
        this.disabled = false;
      }, 1500);
    },
  },
};
</script>
